import React, { useState } from "react";
import { connect } from "react-redux";

import registerUserActions from "../redux/actions/signupActions";

const Signup = (props) => {
  
  const [state, setState] = useState({
    nombre: "",
    correo: "",
    clave: "",
    estado: "activo",
    rol: "comprador"
  });

  const InputChange = e => {
    setState({
      ...state,
      [e.target.name]: e.target.value
    });
  };

  const Submit = e => {
    e.preventDefault();
    const user = {
      nombre: state.nombre,
      correo: state.correo,
      clave: state.clave,
      estado: state.estado,
      rol: state.rol
    };
    props.registerUsers(user)
  };

  return (

      <div className="uk-container uk-container-xsmall">
        <div
          className="uk-grid-collapse uk-child-width-expand@s uk-grid"
          uk-grid="true"
          uk-height-viewport="expand: true"
        >
          <div className="uk-margin-auto-vertical uk-text-center uk-text-center">
            <form className="uk-form-stacked" onSubmit={Submit}>
              <h1>Iniciar Sesión</h1>
              <div className="uk-margin">
                <label className="uk-form-label"></label>
                <div className="uk-form-controls">
                  <input
                    className="uk-input"
                    type="text"
                    name="nombre"
                    placeholder="Nombre Completo"
                    onChange={InputChange}
                  />
                </div>
              </div>
              <div className="uk-margin">
                <label className="uk-form-label"></label>
                <div className="uk-form-controls">
                  <input
                    className="uk-input"
                    type="email"
                    name="correo"
                    placeholder="Correo Electronico"
                    onChange={InputChange}
                  />
                </div>
              </div>
              <div className="uk-margin">
                <label className="uk-form-label"></label>
                <div className="uk-form-controls">
                  <input
                    className="uk-input"
                    type="password"
                    name="clave"
                    placeholder="Contraseña"
                    onChange={InputChange}
                  />
                </div>
              </div>
              <div className="uk-margin">
                <div className="uk-form-controls">
                  <button type="submit" className="uk-button uk-button-primary">
                    Enviar Datos
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    
  );
};

const mapDispatchToProps = dispatch => {
  return {
    registerUsers: (user) => { dispatch(registerUserActions(user)) }
  };
};

export default connect(null, mapDispatchToProps)(Signup);
